<template>
    <div class="container">
        <div class="c-title">
            <p>到账到微信钱包</p>
        </div>

        <div class="line"></div>

        <div class="detail">
            <p class="detail-title">可提现金额</p>
            <p class="detail-money">￥&nbsp;&nbsp;{{money}}</p>
            <p class="detail-tip">*每周三大于100可提现*</p>
        </div>

        <van-button @click="onSubmit" class="confirm">提现</van-button>

    </div>
</template>

<script>
    import {Lazyload, CellGroup, Field, SubmitBar, Button} from 'vant';
    import {FETCH_OPENID, DRIVER_VERIFY, DRAW, GET_DRAW} from '../../common/config'
    import {get, post} from '../../common/http'
    import store from '../../storage'
    import {Toast} from 'vant';

    export default {
        components: {
            [Lazyload.name]: Lazyload,
            [CellGroup.name]: CellGroup,
            [SubmitBar.name]: SubmitBar,
            [Field.name]: Field,
            [Button.name]: Button
        },

        data() {
            return {
                money: 0,
                data: {}
            };
        },

        computed: {},

        methods: {
            initDraw() {
                const params = {phone: this.data.phone}
                post(GET_DRAW, params, res => this.money = res.fee)
            },
            onSubmit() {
                const params = {openId: this.data.openid, money: this.money}
                post(DRAW, params, () => {
                    Toast.success("提现成功")
                })
            }
        },
        created() {
            const data = store.fetch()
            if (data && data.hasOwnProperty("openid")) {
                // has open id?
                this.data = data
                // if (data.hasOwnProperty("id")) {
                //     // init order.
                //     this.initDraw()
                // } else {
                const params = {openId: data.openid}
                // is bind?
                post(DRIVER_VERIFY, params, res => {
                        let resData = res;
                        resData.openid = this.data.openid
                        store.save(resData)
                        this.data = resData
                        this.initDraw()
                    }, () => this.$router.push({name: 'bind', query: {from: 'draw'}})
                );
                // }
            } else {
                const code = store.urlParam('code')
                if (code) {
                    const params = {code}
                    get(FETCH_OPENID, params, res => {
                        this.data.openid = res.openId;
                        store.save(this.data)
                        let param = {openId: this.data.openid}
                        post(DRIVER_VERIFY, param, res => {
                                let resData = res;
                                resData.openid = this.data.openid
                                store.save(resData)
                                this.initDraw()
                            }, () => this.$router.push({name: 'bind', query: {from: 'draw'}})
                        );
                    })
                    ;
                } else {
                    Toast.fail("无法获得授权信息，请退出重试")
                }
            }
        }
    };
</script>

